import { Component,OnInit } from "@angular/core";
import { cservice } from "./cservice";
import { Datatypes } from "./datatypes";
import { ActivatedRoute,Params } from "@angular/router";
import  "rxjs/add/operator/switchMap";

@Component({
    templateUrl:"./update.component.html"
})

export class updateComponent{
   data:Datatypes=new Datatypes()
    constructor(private _cservice:cservice,private _active:ActivatedRoute){}
ngOnInit() {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    this._active.params.switchMap((params:Params)=>this._cservice.getdata(+params['id'])).subscribe(res=>this.data=res);
    
}
}


