import { Datatypes  } from "./datatypes";
import { Injectable } from "@angular/core";

const datatypesvalues:Datatypes[]=[
    {cid:1,cname:"india", cnumber:"000-111-2222"},
    {cid:2,cname:"AUS", cnumber:"222-3333-1111"},
    {cid:3,cname:"NEZ", cnumber:"999-888-4444"},
    {cid:4,cname:"ENG", cnumber:"333-4444-8888"},
    {cid:5,cname:"PAK", cnumber:"555-666-8888"},
]

@Injectable()

export class cservice {
 
    getvalues():Promise<Datatypes[]>{
        return Promise.resolve(datatypesvalues);
    }
    getdata(id:Number):Promise<Datatypes>{
        return this.getvalues().then(res=>res.find(res=>res.cid===id))
    }
    
    adddata(data:Datatypes){
        this.getvalues().then(object=>{
            let maxlength=object.length - 1;
            let addmaxlength= object[maxlength];
            data.cid=addmaxlength.cid + 1;
            object.push(data)
        })

    }


}


